import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import Moment from 'react-moment';
import 'moment/locale/it';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: '#CA2331',
    color: '#FFFFFF',
  },
  cardAction:{
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    }
  }
}));

export default function LastMatch({match, allTeams}) {
  const classes = useStyles();
  
  const title = `${match.home} ${match.goalshome} - ${match.goalsaway} ${match.away}`;
  const homeTeam = allTeams.filter((team) => team.name===match.home);
  const awayTeam = allTeams.filter((team) => team.name===match.away);
  const imageHome = `https://www.archiviorossoblu.it/images/squadre/${homeTeam[0].photo}`;
  const imageAway = `https://www.archiviorossoblu.it/images/squadre/${awayTeam[0].photo}`;
  const image = `https://www.archiviorossoblu.it/images/partite/${match.photo}`;
  //console.log(awayTeam);
  //console.log(awayTeam);
  //console.log(match);
  return (
    <Card className={classes.root}>
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar}>
            {match.day}
          </Avatar>
        }
        title={title}
        subheader={
          <Moment locale='it' format="LLLL">{match.date}</Moment>
        }
      />
      <CardMedia
        className={classes.media}
        image={image}
        title={homeTeam.name}
      />
      <CardActions className={classes.cardAction}>
        <Avatar alt={match.home} src={imageHome} variant="square" />
        <Avatar alt={match.away} src={imageAway} variant="square" />
      </CardActions>
    </Card>
  );
}