import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import LinearProgress from '@material-ui/core/LinearProgress';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.white,
    color: theme.palette.common.black,
  },
  body: {
    fontSize: 12,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles((theme) => ({
  container: {
    maxHeight: 440,  
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  header:{
    fontWeight: 'bold'
  },
})
);

export default function Matches({matches, seasonID, seasonLabel}) {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <Typography className={classes.title} variant="h6" noWrap>
            Campionago Stagione {seasonLabel}
          </Typography>
        </Toolbar>
      </AppBar>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell className={classes.header}>Giornata</StyledTableCell>
              <StyledTableCell className={classes.header}>Data</StyledTableCell>
              <StyledTableCell className={classes.header}>Partita</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
        {matches.length > 0 ? (
              matches.map((row) => (
                <StyledTableRow key={row.id}>
                  <StyledTableCell>{row.day}</StyledTableCell>
                  <StyledTableCell>{row.date}</StyledTableCell>
                  <StyledTableCell>
              {row.home} {row.gh} - {row.ga} {row.away}
                  </StyledTableCell>
                </StyledTableRow>
              ))
        ):(
          seasonID.length>0 ? ( 
          <StyledTableRow key='none'>
            <StyledTableCell align="right" colSpan={4}>
              <LinearProgress />
            </StyledTableCell>  
          </StyledTableRow>  
          ):(
            <StyledTableRow key='none'>
            <StyledTableCell align="right" colSpan={4}>Seleziona stagione.</StyledTableCell>  
          </StyledTableRow>  
          ))}
            </TableBody>
          </Table>
      </TableContainer>
      </div>
  );
}
