import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import LinearProgress from '@material-ui/core/LinearProgress';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.white,
    color: theme.palette.common.black,
  },
  body: {
    fontSize: 12,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  }
}))(TableRow);

const useStyles = makeStyles((theme) => ({
  container: {
    maxHeight: 440,  
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  header:{
    fontWeight: 'bold'
  },
})
);

export default function Players({players, countries, seasonID, seasonLabel}) {
  const classes = useStyles();

  const getFlag = (flag) => {
    const country = countries.filter(country => country.description === flag);
    return country[0].flag;
  }

  console.log("season", seasonID);
  console.log("countries", countries);
  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <Typography className={classes.title} variant="h6" noWrap>
            Rosa Stagione {seasonLabel}
          </Typography>
        </Toolbar>
      </AppBar>
      <Paper className={classes.root}>
        <TableContainer className={classes.container}>      
          <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              <StyledTableCell className={classes.header}>Giocatore</StyledTableCell>
              <StyledTableCell className={classes.header}>Ruolo</StyledTableCell>
              <StyledTableCell className={classes.header}>Numero</StyledTableCell>
              <StyledTableCell className={classes.header}>Presenze</StyledTableCell>
              <StyledTableCell className={classes.header}>Gol</StyledTableCell>
              <StyledTableCell className={classes.header}>Ammonizioni</StyledTableCell>
              <StyledTableCell className={classes.header}>Espulsioni</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
        {players.length > 0 ? (
              players.map((row) => (
                <StyledTableRow key={row.id}>
                  <StyledTableCell component="th" scope="row">
                  <div className='place-avatar' style={{
                    backgroundImage:`url(https://www.archiviorossoblu.it/images/nazioni/${getFlag(row.country)})`, 
                    backgroundRepeat:'no-repeat',
                    margin: 0,
                    paddingLeft: 20,
                    }}>
                    {row.surname} {row.name} 
                  </div>
                  </StyledTableCell>
                  <StyledTableCell>{row.role}</StyledTableCell>
                  <StyledTableCell>{row.number}</StyledTableCell>
                  <StyledTableCell>{row.appearances}</StyledTableCell>
                  <StyledTableCell>{row.goals}</StyledTableCell>
                  <StyledTableCell>{row.yellowcard}</StyledTableCell>
                  <StyledTableCell>{row.redcard}</StyledTableCell>
                </StyledTableRow>
              ))
        ):(
          seasonID.length>0 ? ( 
          <StyledTableRow key='none'>
            <StyledTableCell align="right" colSpan={7}>
              <LinearProgress />
            </StyledTableCell>  
          </StyledTableRow>  
          ):(
            <StyledTableRow key='none'>
            <StyledTableCell align="right" colSpan={7}>Seleziona stagione.</StyledTableCell>  
          </StyledTableRow>  
          ))}
            </TableBody>
          </Table>
      </TableContainer>
      </Paper>
      </div>
  );
}
