import React, { useState, useEffect } from 'react';
import './App.css';
import SeasonDetails from './components/SeasonDetails.js'
import LastMatch from './components/LastMatch'
import Footer from './components/Footer'
import { makeStyles } from '@material-ui/core/styles'
import * as ARBApi from './utils/ARBApi'
import {allCountries} from './utils/allCountries'
import {allTeams} from './utils/allTeams'
import {Grid, Paper, Box} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'justify',
    color: theme.palette.text.secondary,
  },
}));

export default function App() {
  const classes = useStyles();
  
  const [seasonID, setSeasonID] = useState(0);
  const [seasonLabel, setSeasonLabel] = useState('');
  const [seasons, setSeasons] = useState([]);
  const [players, setPlayers] = useState([]);
  const [matches, setMatches] = useState([]);
  const [lastMatch, setLastMatch] = useState([]);

  useEffect(() => {
    ARBApi.getAll(`seasons`).then((seasons) => {
      setSeasons(seasons);
    });
    ARBApi.getAll(`lastMatch`).then((lastMatch) => {
      setLastMatch(lastMatch);
    });
  }, []);

  const changeSeason = (seasonID, year) => {
    ARBApi.getAllByID(`players`, seasonID).then((players) => {
      setPlayers(players);
    });
    ARBApi.getAllByID(`matches`, seasonID).then((matches) => {
      setMatches(matches);
    });
    setSeasonID(seasonID);
    setSeasonLabel(year);
  }

  return( 
    <Box component="span" m={1}>
      <Grid container spacing={1}>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <SeasonDetails 
              players={players} 
              matches={matches} 
              seasons={seasons} 
              countries={allCountries} 
              seasonID={seasonID} 
              seasonLabel={seasonLabel} 
              onSeasonSelect={changeSeason}>
            </SeasonDetails>
          </Paper>
        </Grid>
        {(lastMatch.length>0) && (
        <Grid item xs={3}>
          <Paper className={classes.paper}>
            <LastMatch
              match={lastMatch[0]} 
              allTeams={allTeams}
            />
          </Paper>
        </Grid>
        )
        }
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Footer/>
          </Paper>
        </Grid>
      </Grid>
      </Box>
    )
  }