import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme, withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableHead from '@material-ui/core/TableHead';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableFooter from '@material-ui/core/TableFooter';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Avatar from '@material-ui/core/Avatar';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import { fade } from '@material-ui/core/styles';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import IconButton from '@material-ui/core/IconButton';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import LastPageIcon from '@material-ui/icons/LastPage';
import escareRegExp from 'escape-string-regexp'
import sortBy from 'sort-by'

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  }
}))(TableRow);

const useStyles1 = makeStyles((theme) => ({
  root: {
    flexShrink: 0,
    marginLeft: theme.spacing(2.5),
  },
}));

function TablePaginationActions(props) {
  const classes = useStyles1();
  const theme = useTheme();
  const { count, page, rowsPerPage, onChangePage } = props;

  const handleFirstPageButtonClick = (event) => {
    onChangePage(event, 0);
  };

  const handleBackButtonClick = (event) => {
    onChangePage(event, page - 1);
  };

  const handleNextButtonClick = (event) => {
    onChangePage(event, page + 1);
  };

  const handleLastPageButtonClick = (event) => {
    onChangePage(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
  };

  return (
    <div className={classes.root}>
      <IconButton
        onClick={handleFirstPageButtonClick}
        disabled={page === 0}
        aria-label="first page"
      >
        {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
      </IconButton>
      <IconButton onClick={handleBackButtonClick} disabled={page === 0} aria-label="previous page">
        {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
      </IconButton>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="next page"
      >
        {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
      </IconButton>
      <IconButton
        onClick={handleLastPageButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="last page"
      >
        {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
      </IconButton>
    </div>
  );
}

TablePaginationActions.propTypes = {
  count: PropTypes.number.isRequired,
  onChangePage: PropTypes.func.isRequired,
  page: PropTypes.number.isRequired,
  rowsPerPage: PropTypes.number.isRequired,
};

const useStyles2 = makeStyles((theme) => ({
  avatar: {
    backgroundColor: '#CA2331',
    color: '#FFFFFF',
  },
  container: {
    maxHeight: 440,  
  },
  header:{
    fontWeight: 'bold'
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  }}));

export default function Seasonss({seasons, seasonSelect}) {
  const classes = useStyles2();
  const [query, setQuery] = useState('');
  const [page, setPage] = useState(0);
  const rowsPerPage = 10;

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleQuery = (query) => {
    console.log("query", query);
    setQuery(query);
    setPage(0);
  };

  let filteredSeasons = seasons;
  if (query) {
    const searchMatch = new RegExp(escareRegExp(query),'i');
    filteredSeasons = seasons.filter((season) => searchMatch.test(season.year));
    console.log(filteredSeasons, filteredSeasons);
  }
  filteredSeasons.sort(sortBy('-year'));
  const emptyRows = rowsPerPage - Math.min(rowsPerPage, filteredSeasons.length - page * rowsPerPage);
  return (
    <div className={classes.root}>
      <AppBar position="static">
      <Toolbar>
        <Typography className={classes.title} variant="h6" noWrap>
          Stagioni Cagliari Calcio
        </Typography>
        <div className={classes.search}>
          <div className={classes.searchIcon}>
            <SportsSoccerIcon />
          </div>
          <InputBase
            placeholder="Cerca.."
            classes={{
              root: classes.inputRoot,
              input: classes.inputInput,
            }}
            inputProps={{ 'aria-label': 'search' }}
            value={query}
            onChange={(event => handleQuery(event.target.value))}            
          />
        </div>
      </Toolbar>
    </AppBar>
    <TableContainer className={classes.container}>
      <Table stickyHeader aria-label="sticky table">
        <TableHead>
          <TableRow>
            <TableCell className={classes.header}>Serie</TableCell>
            <TableCell className={classes.header}>Anno</TableCell>
            <TableCell className={classes.header}>Punti</TableCell>
            <TableCell className={classes.header}>Posizione</TableCell>
            <TableCell className={classes.header}>V</TableCell>
            <TableCell className={classes.header}>P</TableCell>
            <TableCell className={classes.header}>S</TableCell>
            <TableCell className={classes.header}>GF</TableCell>
            <TableCell className={classes.header}>GS</TableCell>
            <TableCell className={classes.header}>DR</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {(rowsPerPage > 0
            ? filteredSeasons.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            : filteredSeasons
          ).map((row) => (
            <StyledTableRow key={row.id} onClick={() => seasonSelect(row.id, row.year)}>
              <TableCell>
                <Avatar aria-label="recipe" className={classes.avatar}>
                  {row.league}
                </Avatar>                
              </TableCell>
              <TableCell component="th" scope="row"><a href='#seasons' onClick={ () => seasonSelect(row.id, row.year)}>{row.year}</a></TableCell>
              <TableCell>{row.points}</TableCell>
              <TableCell>{row.position}</TableCell>
              <TableCell>{row.v}</TableCell>
              <TableCell>{row.d}</TableCell>
              <TableCell>{row.l}</TableCell>
              <TableCell>{row.gf}</TableCell>
              <TableCell>{row.gs}</TableCell>
              <TableCell>{row.dr}</TableCell>
            </StyledTableRow>
          ))}
          {emptyRows > 0 && (
            <TableRow style={{ height: 53 * emptyRows }}>
              <TableCell colSpan={6} />
            </TableRow>
          )}
        </TableBody>
        <TableFooter>
          <TableRow>
            <TablePagination
              rowsPerPageOptions={[10]}
              colSpan={10}
              count={filteredSeasons.length}
              rowsPerPage={rowsPerPage}
              page={page}
              SelectProps={{
                inputProps: { 'aria-label': 'rows per page' },
                native: true,
              }}
              onChangePage={handleChangePage}
              ActionsComponent={TablePaginationActions}
            />
          </TableRow>
        </TableFooter>
      </Table>
    </TableContainer>
    </div>
  );
}